.login-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 8px;
  gap: 8px;
  position: relative;
  width: 300px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 5px 2px silver;
